import React,{Component} from 'react';
import Para from './Para';
import  "./App.css";
import  {add,sub,div,mult} from './Cal';
import Card from './Card';



const flname= "Sona";
const curTime=new Date().toLocaleTimeString;
const curDate=new Date().toLocaleDateString;
const img1="https://picsum.photos/200/300";
const img2="https://picsum.photos/240/300";
const links="https://www.javatpoint.com/react-events"
let currDate =new Date();
currDate=currDate.getHours();
let Greeting="";
const cssStyle={};


if(currDate>=1 && currDate<12){
  Greeting="Good Morning";
  cssStyle.color='Green';
}else if (currDate>=12 && currDate<19) {
  Greeting="Good AfterNoon";
  cssStyle.color='Orange';
} else {
  Greeting="Good Night";
  cssStyle.color='Black';
}


class App extends Component {
  render(){
  return (
    <>
    <ul>
         <li>Sum of two no is {add(30,3)}</li>
         <li>sub of two no is {sub(30,3)}</li>
         <li>div of two no is {div(10,3)}</li>
         <li>mult of two no is {mult(10,3)}</li>
           
        </ul>
        <h1>List of top 5 Netflix Series in 2020</h1>
        <Card  imgsrc="https://wallpapercave.com/wp/wp4056410.jpg" title="A Netflix Original series" sname="Dark" link="https://www.netflix.com/in/title/80990668?source=35"/>
    <div className="heading" >
    <h1>Hello Sir,<span style={cssStyle}>{Greeting}</span></h1>
    </div>
    <h1>My name is {flname}</h1>
    <p>Current time is {curTime}</p>
    <p>Todays Date is {curDate}</p>
    <h1>Netflix Pick</h1>
    <p>list of 5 best series</p>
    <Para/>
    <img src={img1} alt="img"/>
    <a href={links} target="randomimg">
    <img src={img2} alt="img"/>
    </a>
    </>
    
  );
  } 
}

export default App;
